  componentDidMount = async () => {
    try {
      // Get network provider and web3 instance.
      const web3 = await getWeb3();

      // Use web3 to get the user's accounts.
      const accounts = await web3.eth.getAccounts();

      // Get the contract instance.
      const networkId = await web3.eth.net.getId();
      const deployedNetwork = CreateDepositContract.networks[networkId];
      const instance = new web3.eth.Contract(
        CreateDepositContract.abi,
        deployedNetwork && '0x097A705A034472DB4Dba4864FD2F34362e7f1c4F',
      );

      // Set web3, accounts, and contract to the state, and then proceed with an
      // example of interacting with the contract's methods.
      this.setState({ web3: web3, accounts: accounts, contract: instance, networkId: networkId });

      document.querySelector('.showAccount').innerHTML = accounts[0].substring(0, 8) + "...";

    } catch (error) {
      // Catch any errors for any of the above operations.
      // window.alert(
      //   `Failed to load web3, accounts, or contract. Check console for details.`,
      // );
      // window.alert("Please log in to metamask first!");
      console.error(error);
    }

  };

  connectMetamask = async () => {
    const provider = await detectEthereumProvider();

    if (provider !== window.ethereum) {
      console.error('Do you have multiple wallets installed?');
    };
    const accounts = await provider.request({ method: 'eth_requestAccounts' });
    document.querySelector('.showAccount').innerHTML = accounts[0];
    document.querySelector('.shuaXin').innerHTML = "Automatically refresh the page after 3 seconds!";

    if (this.state.networkId !== 4) {
      document.querySelector('.testNetwork').innerHTML = "Please switch to the Rinkeby test network!";
    }

    setTimeout(myRefresh, 3000); //3秒
    function myRefresh() {
      window.location.reload();
    }


  };






  startDeposit = async () => {
    const { accounts, contract } = this.state;

    // Stores a given value, 5 by default.
    // await contract.methods.set(5).send({ from: accounts[0] });

    let _jia = this.jia.current.value;
    let _bing1 = this.bing1.current.value;
    let _bing2 = this.bing2.current.value;
    let _bing3 = this.bing3.current.value;
    // let _time = this.time.current.value;
    let _time = this.time.current.value * 8640;
    let _yajin = this.yajin.current.value;

    // mortgageId += 1;

    // await contract.methods.startDeposit(_jia, _bing1, _bing2, _bing3, _time).send({ from: accounts[0] });
    // await web3.eth.sendTransaction({
    //   from: accounts[0],
    //   to: '0xfd16ea9B65F8Dd526Fc7a50924039fF9AcEb2Cba',
    //   value: _yajin,
    //   gas: 21000,
    //   gasPrice: 20000000000,
    // }, (result) => {
    //   console.log(result)
    // })

    document.querySelector(".createDeposit").innerHTML = "Deposit is being created...";

    try {
      await contract.methods.startDeposit(_jia, _bing1, _bing2, _bing3, _time).send({
        from: accounts[0],
        to: '0x097A705A034472DB4Dba4864FD2F34362e7f1c4F',
        value: _yajin * 10000000000000000 //押金以0.01ether为单位
        // gas: 21000,
        // gasPrice: 20000000000,
      });
      await contract.events.CreateDeposit({
        fromBlock: 8363732
      }, function (error, event) {
        console.log(event);
        console.log(error);

        document.querySelector(".createDeposit").innerHTML = "Created successfully! Your deposit ID is" + event.returnValues[1];
      });
    } catch (error) {
      console.log(error);
      if (error.message === "MetaMask Tx Signature: User denied transaction signature.") {
        document.querySelector(".createDeposit").innerHTML = "User cancelled!";
      } else if (error.code === -32602) {
        document.querySelector(".createDeposit").innerHTML = "Please refresh the page and try again!";
        window.alert("Please refresh the page and try again!");
      } else {
        document.querySelector(".createDeposit").innerHTML = "Please enter the correct wallet address and check your network!";
      };

    };

    // Update state with the result.
    // this.setState({ storageValue: response });
  };






