  withdrawQuarter = async () => {
    const { accounts, contract } = this.state;

    let _mortgageId = this.mortgageId3.current.value;

    document.querySelector(".withdrawQuarter").innerHTML = "Deposit withdrawing...";

    try {
      await contract.methods.withdrawQuarter(_mortgageId).send({
        from: accounts[0]
      }).then(function (result) {
        console.log(result);
        if (result.status === true) {
          document.querySelector(".withdrawQuarter").innerHTML = "A quarter of the deposit is successfully withdrawn!";
        } else {
          document.querySelector(".withdrawQuarter").innerHTML = "Failed to withdraw a quarter of the deposit!";
        };

      });
    } catch (error) {
      console.log(error);
      if (error.message === "MetaMask Tx Signature: User denied transaction signature.") {
        document.querySelector(".withdrawQuarter").innerHTML = "User cancelled!";
      } else if (error.code === -32602) {
        document.querySelector(".withdrawQuarter").innerHTML = "Please refresh the page and try again!";
        window.alert("Please refresh the page and try again!");
      } else {
        document.querySelector(".withdrawQuarter").innerHTML = "The refund failed, a quarter of the deposit has been refunded or the time is not up!";
      };
    }


  };

