  withdrawOnTime = async () => {
    const { accounts, contract } = this.state;

    let _mortgageId = this.mortgageId2.current.value;

    document.querySelector(".withdrawOnTime").innerHTML = "Deposit withdrawing...";

    try {
      if (_mortgageId === "") {
        document.querySelector(".withdrawOnTime").innerHTML = "Please enter the correct deposit ID!";
      } else {
        await contract.methods.withdrawOnTime(_mortgageId).send({
          from: accounts[0]
        });
      }

      await contract.events.FinishDeposit({
        fromBlock: 8363732
      }, function (error, event) {
        console.log(error);
        console.log(event);
        if (event.returnValues[2] !== "0") {
          document.querySelector(".withdrawOnTime").innerHTML = "Successful deposit withdrawal!";
        } else {
          document.querySelector(".withdrawOnTime").innerHTML = "The deposit withdrawal failed and the deposit has been refunded!";
          window.alert("The deposit has been refunded, please do not claim it again!");
        };

      });
    } catch (error) {
      console.log(error);
      if (error.message === "MetaMask Tx Signature: User denied transaction signature.") {
        document.querySelector(".withdrawOnTime").innerHTML = "User cancelled!";
      } else if (error.code === -32602) {
        document.querySelector(".withdrawOnTime").innerHTML = "Please refresh the page and try again!";
        window.alert("Please refresh the page and try again!");
      } else {
        document.querySelector(".withdrawOnTime").innerHTML = "Time is not up!";
      };
    }


  };


